div.slide-right {
    width:100%;
    /* background: rgba(153, 205, 50, 0.473); */
    background: rgb(2,0,36);
background: linear-gradient(90deg, rgba(2, 0, 36, 0.096) 0%, rgba(0, 255, 42, 0.219) 20%, rgb(251, 255, 0) 68%);
    font-weight: bold;
    overflow:hidden;
    /* font-size: large; */
    margin-top:0px;
    margin: 0px;
    padding: 0px;
    transform: skewY(4deg);

  }
  div.slide-right p {
    animation: 30s slide-left infinite;
  }
  
  @keyframes slide-left {
    from {
      margin-left: 50%;
      width: 1100%; 
    }
  
    to {
      margin-left: -200%;
      width: 1100%;
    }
  }
